export default function SignIn() {
    const [alert, setAlert] = useState([])
    const [price, setPrice] = useState(0)
    const [currency, setCurrency] = React.useState('DOGEUSDT');
    const [condition, setCondition] = React.useState('>');
    const [isUpdate, setIsUpdate] = useState(false)
    const [idEdit, setIdEdit] = useState(null)

    useEffect(()=> {
        initial()
    },[])

    const initial =async () => {
        console.log("in initial");
        let result = await API.getAlerts()
        setAlert(result.data)
        console.log("ð ~ file: Home.js ~ line 77 ~ initial ~ result", result)
    }

    const handleChange = (event) => {
        setCurrency(event.target.value);
    };
    const handleChangeConn = (event) => {
        setCondition(event.target.value);
    };

    const onAddAlert = (data = {}) => {
        if (isUpdate) {
            let temp = alert.map(ele => {
                if (ele.id === idEdit) {
                    return {
                        coin: currency,
                        condition,
                        price: +price,
                        isAlert: false,
                        id: idEdit
                    }
                }
                return {
                    ...ele
                }
            })
            setAlert([...temp])
        } else {
            const temp = {
                id: alert.length + 1,
                coin: currency,
                condition,
                price: +price,
                isAlert: false
            }
            let temps = sortBy([...alert, temp], 'price')
            setAlert([...temps])
        }
        setIsUpdate(false)
    }
    const onClickEdit = (data) => {

        setIsUpdate(true)
        setPrice(data.price)
        setCurrency(data.coin)
        setCondition(data.condition)
        setIdEdit(data.id)

    }

    const onKeyPress = (event) => {
        if (event.key === 'Enter') {
            onAddAlert()
        }
    }

    const onClickDelete = (id) => {
        let delList = alert.filter(ele => ele.id != id)
        setAlert([...delList])
    }

    const mergeProps = {
        onClickEdit,
        onClickDelete,
        alertList: alert,
    }

    const onClickSave = async () => {
        let result = await API.saveAlerts(alert)
        console.log("ð ~ file: Home.js ~ line 148 ~ onClickSave ~ result", result.data)
    }


    return (
        <>

            <Nav/>
        <Container component="main" maxWidth="xs" style={{ marginTop: 50 }}>
            <Grid container spacing={1}>
                <Grid item xs={3}>
                    <TextField
                        id="standard-select-currency"
                        select
                        // label="Select"
                        value={currency}
                        onChange={handleChange}
                    // helperText="Please select your currency"
                    >
                        {currencies.map((option) => (
                            <MenuItem key={option.value} value={option.value}>
                                {option.label}
                            </MenuItem>
                        ))}
                    </TextField>
                </Grid>
                <Grid item xs={3}>
                    <TextField
                        id="standard-select-currency"
                        select
                        // label="Select"
                        value={condition}
                        onChange={handleChangeConn}
                    // helperText="Please select your currency"
                    >
                        {conditionList.map((option) => (
                            <MenuItem key={option.value} value={option.value}>
                                {option.label}
                            </MenuItem>
                        ))}
                    </TextField>
                </Grid>
                <Grid item xs={3}>
                    <TextField
                        size="small"
                        id="outlined-basic"
                        label="Price"
                        variant="outlined"
                        value={price}
                        onChange={({ target: { value } }) => setPrice(value)}
                        onKeyPress={onKeyPress}
                    />
                </Grid>
                <Grid item xs={3}>
                    {
                        isUpdate ?
                            <Button variant="contained" color="secondary" onClick={onAddAlert} onKeyPress={onKeyPress}>
                                à¹à¸à¹à¹à¸
                            </Button>
                            :
                            <Button variant="contained" color="à¸à¸à¸" onClick={onAddAlert} onKeyPress={onKeyPress}>
                                à¹à¸à¸´à¹à¸¡
                            </Button>
                    }

                </Grid>
            </Grid>
            <hr />
            <List {...mergeProps} />
            <hr style={{marginTop: '20px'}}/>
            <Button variant="contained" style={{backgroundColor: '#07ca63', color: 'white'}} onClick={onClickSave}>
                à¸à¸±à¸à¸à¸¶à¸
            </Button>
        </Container>
        </>
    );
}