import React, { useEffect, useState } from 'react';
import ListItem from './ListItem'

export default function ListDividers({ alertList = [], onClickEdit, onClickDelete, }) {
    const props = {
        onClickEdit,
        onClickDelete
    }

    return (
        <>
            {
                alertList.map((ele, index) => <ListItem {...{ ...props, ...ele, index }} />)
            }

        </>
    );
}