import axios from 'axios'


const BASE_URL = 'http://167.99.69.36:5000'

const getAlerts = async () => {
    let result = await axios.get(`${BASE_URL}/alerts`)
    return result
}

const saveAlerts = async (data) => {
    let result = await axios.post(`${BASE_URL}/alerts`, { alertList: data })
    return result
}
export default {
    getAlerts,
    saveAlerts,
}