import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import { Grid } from '@material-ui/core';

export default function ListDividers(props) {
    const { coin, condition, price, id, isAlert, onClickEdit, onClickDelete } = props
    let color = 'black'
    if(condition === '>'){
        color = 'green'
    }else if(condition === '<'){
        color = 'red'
    }
    return (
        <Grid container alignContent='center' alignItems='center' className='card-item' style={{marginTop: '14px'}}>
            <Grid item xs={3}>
                {coin}
            </Grid>
            <Grid item xs={1}>
                <span style={{ fontWeight: 'bolder', fontSize: '20px', color: color }}> {condition} </span>
            </Grid>
            <Grid item xs={2}>
               <span style={{ fontWeight: 'bolder', fontSize: '20px', color: color }}> {price} </span> 
            </Grid>
            <Grid item xs={6} style={{ display: 'flex', justifyContent: 'flex-end' }}>
                <Grid item={6} className='item-icon' style={{ color: '#002bff'}} onClick={() => onClickEdit({coin, condition, price, id, isAlert})}>
                    <i class="fa fa-edit"></i>
                </Grid>
                <Grid item={6} className='item-icon' style={{marginLeft: '15px', color: 'red'}}>
                    <i class="fa fa-trash-o" onClick={() => onClickDelete(id)}></i>
                </Grid>
            </Grid>
        </Grid>
    );
}